.neo-code {
  font-family: Roboto Mono, monospace;
}

h1, h2, h3, h4, h5, h6 {
  color: var(--heading-color);
  margin-top: 0;
  font-weight: normal;
}

h1 {
  font-size: 44px;
  line-height: 64px;
}

h1:last-child {
  margin-bottom: 0;
}

h1.neo-paragraph-spacing {
  margin-bottom: 17.6px;
}

