  font-size: 80px;
  line-height: 120px;
}

h1.jumbo.neo-paragraph-spacing {
  margin-bottom: 160px;
}

.neo-subtitle {
  margin-bottom: 0;
  padding-top: 10px;
  padding-bottom: 10px;
  line-height: 19.6px;
}

small {
  font-size: 80%;
  line-height: inherit;
}

p {
  color: var(--global-font-color);
  letter-spacing: .05px;
  text-rendering: optimizelegibility;
  margin-top: 0;
  font-weight: normal;
}

p:before {
  margin-left: 0;
  margin-right: 4px;
}

[dir="rtl"] p:before {
  margin-left: 4px;
  margin-right: 0;
}

p.neo-paragraph-spacing {
  margin-bottom: 28px;
}

a, .neo-link {
  color: var(--link-color);
  text-decoration: none;
}

a.neo-link--inline, .neo-link.neo-link--inline {
  text-decoration: underline;
}

a.neo-link--inline:hover:not([class*="disabled"]), .neo-link.neo-link--inline:hover:not([class*="disabled"]) {
  text-decoration: none;
}

a:hover:not([class*="disabled"]), .neo-link:hover:not([class*="disabled"]) {
  color: var(--link-hover-color);
  cursor: pointer;
  text-decoration: underline;
  transition: color .4s;
}

dl, ol, ul {
  margin-top: 0;
  margin-bottom: 6px;
  margin-left: 6px;
  padding: 0;
}

dl dt {
  font-weight: bold;
}

dl dd {
  margin: 0 0 3px;
}

hr {
  border: 1px solid var(--neo-global-border-color);
  clear: both;
  border-top-width: 0;
  border-left-width: 0;
  border-right-width: 0;
  max-width: 75rem;
  height: 0;
  margin: 1.25rem auto;
}

.neo-text-capitalized {
