.neo-right-text {
  text-align: right;
}

.neo-spinner {
  color: var(--spinner-color);
  vertical-align: middle;
  align-self: center;
  display: inline-block;
  position: relative;
}

.neo-spinner:after {
  content: "";
  vertical-align: middle;
  font-family: updated-neo-icons;
  animation: .5s infinite spinner-animation;
  display: inline-block;
}

.neo-btn > .neo-spinner, .neo-multiselect__header > .neo-spinner {
  color: inherit;
  margin-right: 6px;
}

[dir="rtl"] .neo-btn > .neo-spinner, [dir="rtl"] .neo-multiselect__header > .neo-spinner {
  margin-left: 6px;
  margin-right: initial;
}

.neo-spinner:after {
  font-size: 20px;
}

.neo-spinner--large:after {
  font-size: 26px;
}

.neo-spinner--x-large:after {
  font-size: 34px;
}

.neo-arrow {
  width: 15px;
  height: 7.5px;
  display: block;
  position: absolute;
}

