// new empty state class

.neo-empty-state {
  @include NeoLinkStyles;
  color: $empty-state-color;
  font-size: $empty-state-font-size;
  font-weight: $empty-state-font-weight;
  left: 0;
  // overflow-x: hidden;
  position: absolute;
  right: 0;
  text-align: center;
  top: 50%;
  transform: translateY(-50%);

  & p {
    color: $empty-state-color;
    font-size: 19px;
  }

  @include neo-icon {
    color: inherit;
    font-size: $empty-state-icon-size;
    margin-bottom: $global-spacer-large;
    width: 100%;
  }

  & p::before {
    margin-bottom: 21px !important;
  }

  > *:last-child {
    margin-bottom: 0;
  }
}
