.neo-breadcrumbs {
  @include NeoLinkStyles;
  display: flex;
  justify-content: space-between;
  & .neo-breadcrumbs__wrapper {
    display: flex;
    flex-direction: column;
    & .neo-breadcrumbs__description {
      color: $breadcrumbs-default-color;
      font-size: $breadcrumbs-subtext-font-size;
      font-weight: $breadcrumbs-subtext-font-weight;
      letter-spacing: $breadcrumbs-subtext-letter-spacing;
      line-height: $breadcrumbs-subtext-line-height;
      margin: $breadcrumbs-subtext-spacing 0px 0px 0px;
    }
  }
  & ol {
    flex-grow: 1;
    margin: 0;
    padding-left: 0;
    list-style: none;
    & .neo-breadcrumbs__link {
      display: inline;
      & + .neo-breadcrumbs__link::before {
        display: inline-block;
        margin: 0px $breadcrumb-slash-spacing;
        content: "/";
        font-size: $breadcrumbs-link-font-size;
        font-weight: $breadcrumbs-link-font-weight;
        letter-spacing: $breadcrumbs-link-letter-spacing;
        line-height: $breadcrumbs-link-line-height;
      }
      & a {
        font-size: $breadcrumbs-link-font-size;
        font-weight: $breadcrumbs-link-font-weight;
        letter-spacing: $breadcrumbs-link-letter-spacing;
        line-height: $breadcrumbs-link-line-height;
        text-decoration: none;
        &:hover {
          color: $breadcrumbs-hover-color;
        }
      }
      &--current a {
        color: $breadcrumbs-default-color;
        font-size: $breadcrumbs-link-font-size;
        font-weight: $breadcrumbs-link-font-weight;
        letter-spacing: $breadcrumbs-link-letter-spacing;
        line-height: $breadcrumbs-link-line-height;
        text-decoration: none;
        pointer-events: none;
        &:hover {
          color: $breadcrumbs-default-color;
        }
      }
    }
  }

  & .neo-breadcrumbs__actions {
    display: flex;
    align-self: center;
    & .neo-btn:not(:last-child) {
      margin-right: $token-breadcrumb-breadcrumb-button-spacing;
      @include rtl-selector {
        margin-right: 0px;
        margin-left: $token-breadcrumb-breadcrumb-button-spacing;
      }
    }
  }
}
